import ssCalendar from '@/components/ss-calendar/ss-calendar.vue'
export default {
	components: {
	  ssCalendar
	},
	data() {
		return {
			checks: [],
			today: '',
			memberId: '',
			trainInfo:{}
		};
	},
	mounted(){
		this.memberId = JSON.parse(uni.getStorageSync('userInfo')).id
		this.getMemberSignRecord()
	},
	methods: {
		// 获取签到记录
		getMemberSignRecord(){
			this.$Request.postT('/appSignController.do?getMemberSignRecord',{
				memberId: this.memberId
			}).then(res => {
				if(res.code == 0){
					this.trainInfo = res.data
					this.checks = res.data.signList
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		gotoUrl(url){
			uni.navigateTo({
				url: url
			})
		},
		onSignInBtn(){ // 用户签到
			this.$Request.postT('/appSignController.do?memberTodaySign',{
				memberId: this.memberId
			}).then(res => {
				if(res.code == 0){
					this.getMemberSignRecord()
				}
				uni.showToast({
					title: res.info,
					icon: 'none'
				})
			})
		},
		onSignIn(payload) {
		  const { checked, year, month, day, isToday } = payload
		  // !checked && this.checks.push(`${year}-${month}-${day}`)
		  if(isToday == true && !checked){
			  this.$Request.postT('/appSignController.do?memberTodaySign',{
			  	memberId: this.memberId
			  }).then(res => {
			  	if(res.code == 0){
					this.getMemberSignRecord()
			  	}
			  	uni.showToast({
			  		title: res.info,
			  		icon: 'none'
			  	})
			  })
		  }else if(isToday == true && checked){
			  uni.showToast({
			  	title: '今天已经签过到了，不能重复签到',
			  	icon: 'none'
			  })
		  }else{
			  uni.showToast({
			  	title: '只可签到当日日期！',
			  	icon: 'none'
			  })
		  }
		  console.log('onSignIn', payload)
		},
		// 点击领取能量
		clickReceive(){
			this.$Request.postT('/appSignController.do?exchangeOneDayMember',{
				memberId: this.memberId
			}).then(res => {
				if(res.code == 0){
					this.getMemberSignRecord()
				}
				uni.showToast({
					title: res.info,
					icon: 'none'
				})
			})
		},
		onPrev(payload) {
		  console.log('onPrev', payload)
		},
		onNext(payload) {
		  console.log('onNext', payload)
		},
	}
}
